@charset "UTF-8";
/* リキッドレイアウト対応 */
body {
  font-family: "Noto Sans jp", serif;
  color: #4A3636;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.6427104723vw;
  }
}
@media (min-width: 974px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
  padding: 0;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.fv {
  width: 100%;
  position: relative;
}

.fv_img img {
  height: 34.625rem;
}

.fv_inner {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 23.4375rem;
  margin: 15px auto;
  padding: 0 20px;
}

.fv_text {
  font-family: "Kiwi Maru", serif;
  font-size: 14px;
  line-height: 1.25;
  text-align: center;
}

.fv_title {
  margin-top: 20px;
}

.fv_title_bottom {
  margin: 15px auto 0;
  display: block;
  width: 15rem;
}

.fv_icon {
  display: flex;
  margin-top: 15px;
}

.fv_icon_left {
  width: 8.875rem;
  height: 5rem;
}

.fv_icon_right {
  width: 6.875rem;
  height: 6.875rem;
  margin-left: 50px;
}

.fv_smartphone {
  width: 12.5rem;
  margin: -10px auto 0;
  position: relative;
}

.fv_box {
  width: 10rem;
  height: 9.625rem;
  border-radius: 8px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.fv_box_title {
  font-size: 0.75rem;
  color: #fff;
  text-align: center;
  padding: 10px 0;
  background-color: #D65A5C;
  border-radius: 8px 8px 0 0;
}

.header {
  width: 100%;
  height: 64px;
  background-color: #fff;
}

.header_inner {
  display: flex;
  justify-content: space-between;
  padding: 18px 16px;
}

.header_logo {
  width: 210px;
  height: 27px;
}

.header_button_sp {
  width: 2rem;
  height: 1.75rem;
  position: relative;
  display: block;
}

.header_button_sp span {
  display: block;
  position: absolute;
  height: 3px;
  width: 100%;
  border-radius: 8px;
  background: #4A3636;
  transition: all 0.4s ease;
}

.header_button_sp span:nth-of-type(1) {
  top: 0px;
}

.header_button_sp span:nth-of-type(2) {
  top: 13px;
}

.header_button_sp span:nth-of-type(3) {
  top: 26px;
}

.header_button_sp.active span:nth-of-type(1) {
  top: 50%;
  transform: rotate(-45deg);
}

.header_button_sp.active span:nth-of-type(2) {
  opacity: 0;
}

.header_button_sp.active span:nth-of-type(3) {
  top: 50%;
  transform: rotate(45deg);
}

.inner {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1024px;
    padding-right: 25px;
    padding-left: 25px;
  }
}